/**
 * 环境配置
 *
 * @type {string}
 */

let baseUrl = 'http://127.0.0.1';

if (process.env.NODE_ENV === "development") {
    baseUrl = "";
} else if (process.env.NODE_ENV === "test") {
    baseUrl = "";
} else if (process.env.NODE_ENV === "production") {
    baseUrl = "";
}

let isProduction = false;

if (process.env.NODE_ENV === "production") {
    isProduction = true;
}

export {
    baseUrl,
    isProduction
}
